import React, { Component } from 'react';

export default class Time extends Component {

	makeMonth = currentMoth => {
		let month = null;

		switch(currentMoth) {
			case 0:
				month = 'янв';
				break;
			case 1:
				month = 'фев';
				break;
			case 2:
				month = 'мар';
				break;
			case 3:
				month = 'апр';
				break;
			case 4:
				month = 'май';
				break;
			case 5:
				month = 'июн';
				break;
			case 6:
				month = 'июл';
				break;
			case 7:
				month = 'авг';
				break;
			case 8:
				month = 'сен';
				break;
			case 9:
				month = 'окт';
				break;
			case 10:
				month = 'ноя';
				break;
			case 11:
				month = 'дек';
				break;
		} 	

		return month;
	};

	makeDay = currentDay => {
		let day = null;

		switch(currentDay) {
			case 0:
				day = 'воскресенье';
				break;
			case 1:
				day = 'понедельник';
				break;
			case 2:
				day = 'вторник';
				break;
			case 3:
				day = 'среда';
				break;
			case 4:
				day = 'четверг';
				break;
			case 5:
				day = 'пятница';
				break;
			case 6:
				day = 'суббота';
				break;
		} 	

		return day;
	};

	render() {
		let date = new Date();

		return (
			<div className = 'time'>
				<div className = 'date'>
					<div className = 'date-day'>{ date.getDate() }</div>
					<div className = 'date-info'>
						<span className = 'date-info__month'>{ this.makeMonth(date.getMonth()) }</span>
						<span className = 'date-info__year'>{ date.getFullYear() }</span>
					</div>
				</div>
				<div className = 'time-week'>{ this.makeDay(date.getDay()) }</div>
			</div>
		);
	}
}