import React, { Component } from 'react';

export default class TodoListItem extends Component {

	render() {
		const { text, done, clickTarget, deleteTarget, id } = this.props;
		let itemClass, faDone, faDelete, deleteButton = null;
		let doneButton = <button className = 'todo-list-item__button wait-btn' onClick = { () => { clickTarget(id) } }></button>;
		
		if (done) {
			itemClass = 'todo-list-item done';

			faDone = <i className = 'fa fa-check' aria-hidden = 'true'></i>;
			faDelete = <i className = 'fa fa-times' aria-hidden = 'true'></i>;

			doneButton = <button className = 'todo-list-item__button done-btn' onClick = { () => { clickTarget(id) } }>{ faDone }</button>;
			deleteButton = <button className = 'todo-list-item__button delete-btn' onClick = { () => { deleteTarget(id) } }>{ faDelete }</button>;

		} else {
			itemClass = 'todo-list-item no-done';
		}

		return (
			<div className = { itemClass }>
				<div className = 'todo-list-item__text'>{ text }</div>
			    <div className = 'todo-list-item__buttons'>
			    	{ doneButton }
			    	{ deleteButton }
			    </div>
			</div>
		);
	}
}