import React, { Component } from 'react';

import TodoListItem from './todo-list-item';


export default class TodoList extends Component {

	render() {
		const { targets, clickTarget, deleteTarget, inputState, addNewTarget, changeInputValue } = this.props;
		let items, input = null;


		if (targets !== null) {
			items = targets.map((target, index) => {
				return (
					<TodoListItem 
						text = { target.text } 
						done = { target.done } 
						key = { index } 
						id = { index } 
						clickTarget = { clickTarget } 
						deleteTarget = { deleteTarget }
					/>
				);
			});
		}

		if (inputState) input = (
			<div className = 'new-item-form'>
				<input className = 'new-item-form__input' placeholder = 'Что нибудь сделать' onChange = { changeInputValue } maxlength = '20'></input>
				<button className = 'todo-list-item__button done-btn' onClick = { addNewTarget }>
					<i className = 'fa fa-check' aria-hidden = 'true'></i>
				</button>
			</div>
		);

		return (
			<div className = 'todo-list'>
				{ items }
				{ input }
			</div>
		);
	}
}