import React, { Component } from 'react';
import ReactDOM from 'react-dom';

import TodoList from './components/todo-list';
import Time from './components/time';

import './style.css';

class App extends Component {

	state = { 
		targets: JSON.parse(localStorage.getItem('targets')),
		input: '',
		inputState: false
	};

	changeInputValue = event => {
		let value = event.target.value;
		value = value.trim();
		this.setState({ input: value });
	};

	addNewTarget = () => {
		let input = this.state.input;

		if (input === '' || input.lenth === 0) return;

		let newTarget = { text: input, done: false };

		let targets = Object.assign([], JSON.parse(localStorage.getItem('targets')));
		targets.push(newTarget);

		localStorage.setItem('targets', JSON.stringify(targets));
		this.setState({ 
			targets: JSON.parse(localStorage.getItem('targets')), 
			input: '', 
			inputState: false 
		});
	};

	deleteTarget = id => {
		let targets = Object.assign([], JSON.parse(localStorage.getItem('targets')));
		targets.splice(id, 1);

		localStorage.setItem('targets', JSON.stringify(targets));
		this.setState({ targets: JSON.parse(localStorage.getItem('targets')) });
	};

	showInput = () => { this.setState(({ inputState }) => ({ inputState: !inputState })) };

	clickTarget = id => {
		let targets = Object.assign([], JSON.parse(localStorage.getItem('targets')));
		targets[id].done = !targets[id].done;

		localStorage.setItem('targets', JSON.stringify(targets));
		this.setState({ targets: JSON.parse(localStorage.getItem('targets')) });
	};

	render() {
		const { targets, inputState } = this.state;

		return (
			<div id="app">
				<Time />
				<TodoList 
					targets = { targets } 
					clickTarget = { this.clickTarget } 
					deleteTarget = { this.deleteTarget } 
					inputState = { inputState } 
					addNewTarget = { this.addNewTarget }
					changeInputValue = { this.changeInputValue }  
				/>
				<button id = 'add-item' onClick = { this.showInput }>+</button>
			</div>
		);
	}
}

ReactDOM.render(<App />, document.getElementById('main-wrap'));